import React, { useEffect, useState } from 'react'
import {createUseStyles} from 'react-jss'
import {OnboardNavbar} from './OnboardNavbar'
import {Container} from './Container'
import {Button} from 'antd'
import {User} from '../model/model'
import {myFirebase} from "../lib/firebase";
import {Form1} from './OnboardingFlow/Form1'

const TOTAL_STEPS = [
    {title: "Get Started", desc: "Add Personal Data"},
    {title: "Add Socials", desc: "Add Social Networks"},
    {title: "Take the Quiz", desc: "Finish Up and Get Evaluated"}
];

export function Onboard() {
    const [currentStep, changeStep] = useState(0);
    const [currentUser, changeUser] = useState<User | undefined>(undefined)

    useEffect(() => {
        console.log(myFirebase.auth().currentUser)
    });

    var currentForm;
    switch (currentStep) {
        case 0:
            currentForm = <Form1 currentUser={currentUser} changeCurrentUser={(user) => changeUser(user)}/>
            break;
        case 1:
            currentForm = <Form1 currentUser={currentUser} changeCurrentUser={(user) => changeUser(user)}/>
            break;
        case 2:
            currentForm = <Form1 currentUser={currentUser} changeCurrentUser={(user) => changeUser(user)}/>
            break;
    }

    return (
        <Container>
            <OnboardNavbar currentStep={currentStep} steps={TOTAL_STEPS}/>
            {currentForm}
            <Button style={{width: 100}}>{currentStep !== 2 ? 'Next Step' : 'Take the Quiz'}</Button>
        </Container>
    );
}